#search-box {
  font: inherit;
  max-width: 320px;
  color: #dbd7d7;
  background-color: #585a63;
  border: none;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  outline: none;
  margin-bottom: 16px;
  padding: 8px;
}

.search-container {
  flex-direction: row;
  gap: 20px;
  display: flex;
}

#search-box {
  padding: 15px 20px;
  transition: outline, all cubic-bezier(.4, 0, 1, 1), all .5s;
}

#search-box::-ms-input-placeholder {
  color: #dbd7d7;
  padding: 0 5px;
}

#search-box::placeholder {
  color: #dbd7d7;
  padding: 0 5px;
}

#search-box:focus {
  outline: 2px solid #ff4700;
}

#search-box:focus + .spinner-container .spinner {
  width: 40px;
  height: 40px;
  border-right: 3px solid #ff4700;
  border-radius: 50%;
  animation: spinnerRotate 2s linear infinite;
  display: inline-block;
}

@keyframes spinnerRotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

